@import "sass/index";
@import "config/themes/_diamonds";

@mixin paintStand($color) {
  @debug "#{$color}";
  & > path {
    fill: rgba($color, 0.5);
  }
}

.root {
  position: relative;
  pointer-events: none;
  user-select: none;
}
.diamond {
  position: relative;
  top: 45px;
  text-align: center;
  user-select: none;
  animation: hovering 1.2s linear infinite, emergence 0.3s linear;
  & > img {
    user-drag: none;
  }
}
.stand {
  & > svg {
    position: relative;
    z-index: -1;
  }
  text-align: center;
}
.redStand {
  @include paintStand($redDiamondColor);
}
.pinkStand {
  @include paintStand($pinkDiamondColor);
}
.greenStand {
  @include paintStand($greenDiamondColor);
}
.yellowStand {
  @include paintStand($yellowDiamondColor);
}
.blueStand {
  @include paintStand($azureDiamondColor);
}
.cyanStand {
  @include paintStand($blueDiamondColor);
}
.purpleStand {
  @include paintStand($purpleDiamondColor);
}
.winCombination {
  & > img {
    filter: brightness(1.5);
  }
}
@keyframes hovering {
  from {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-4px);
  }
  to {
    transform: translateY(0);
  }
}
.diamondShadow {
  position: absolute;
  left: 40px;
  bottom: 42px;
  animation-name: shadow;
  animation-duration: 1.2s;
  animation-iteration-count: infinite;
}
.greenDiamondShadow {
  left: 53px;
}
@keyframes shadow {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
@keyframes emergence {
  from {
    opacity: 0;
    transform: translateY(-40px);
  }
  50% {
    opacity: 1;
  }
  to {
    transform: translateY(0);
  }
}
